@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 650px) and (max-width: 959px)";

// Normalize Styles
@import (inline) 'node_modules/normalize.css/normalize.css';
@import (inline) 'node_modules/font-awesome/css/font-awesome.min.css';
@import (inline) 'node_modules/animate.css/animate.min.css';


//Import Vendor Styles
// @import (inline) 'node_modules/gridly/dist/prefixed/gridly-core.min.css';
// @import 'node_modules/bootstrap-less/bootstrap/index';

// Import Modules
@import '../_modules/header/header';
@import '../_modules/footer/footer';
@import '../_modules/horizontal-nav/horizontal-nav';

// Import Peripheral Styles
@import 'fonts';
@import 'mixins';


//
body {
    overflow: hidden;
    background: #edf9f9;
}

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

//Resting ul styles
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

#content-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}


//Import Styles
@import 'index';
@import 'contact';
@import 'about';
@import 'blog';
@import 'portfolio';


@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@-webkit-keyframes moveInFromLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-50px);
        transform: translateX(-50px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes moveInFromLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-50px);
        transform: translateX(-50px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

@-webkit-keyframes moveOutToLeft {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(-25%);
        transform: translateX(-25%);
    }
}

@keyframes moveOutToLeft {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(-5%);
        transform: translateX(-5%);
    }
}

@-webkit-keyframes moveInFromRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(50px);
        transform: translateX(50px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes moveInFromRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(50px);
        transform: translateX(50px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

@-webkit-keyframes moveOutToRight {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(5%);
        transform: translateX(5%);
    }
}

@keyframes moveOutToRight {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(5%);
        transform: translateX(5%);
    }
}


[data-viewport] {
    -webkit-animation-duration: .4s;
    animation-duration: .4s;
    transition-timing-function: ease-in;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.is-exiting [data-viewport] {
    transition-timing-function: ease-out;
}

#content-wrapper[data-transition=fade] [data-viewport] {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
}

#content-wrapper[data-transition=fade].is-exiting [data-viewport] {
    -webkit-animation-name: fadeOut;
    animation-name: fadeOut;
}

#content-wrapper[data-transition=moveleft] [data-viewport] {
    -webkit-animation-name: moveInFromLeft;
    animation-name: moveInFromLeft;
}

#content-wrapper[data-transition=moveleft].is-exiting [data-viewport] {
    -webkit-animation-name: moveOutToRight;
    animation-name: moveOutToRight;
}

#content-wrapper[data-transition=moveright] [data-viewport] {
    -webkit-animation-name: moveInFromRight;
    animation-name: moveInFromRight;
}

#content-wrapper[data-transition=moveright].is-exiting [data-viewport] {
    -webkit-animation-name: moveOutToLeft;
    animation-name: moveOutToLeft;
}