@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 650px) and (max-width: 959px)";


#index {
	height: 100%;
  background: url('../images/backgrounds/index.jpg');
  background-size: cover;
  background-color: #eff8f9;

  height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;



  .left-side {
		display: flex;
		flex: 1;
		flex-direction: column;
    justify-content: space-between;
  }

  

  .content {
  	height: 100%;
  	display: flex;
  	// flex-direction: column;
  	padding-top: 10vh;
  	& > * {
  		// flex: 1
  	}

  	.main-content {
  		display: flex;
	  	flex-direction: column;
			align-items: stretch;
			flex: 1;
  		& > * {
  			// display: flex;
	  		// flex: 1
	  	}

	  	

  		// .top {
  		// 	display: flex;
			 //  flex-flow: column;
				// height: 100%;
			 //  flex: 3;
			 //  // padding-top: 10%;

				// .headline {
				// 	// position: relative;
				// 	// top: 0;
				// 	// right: 0;


				// 	display: flex;
	  	// 		flex-direction: row;
  		// 		justify-content: flex-end;


  		// 		// .first {
  		// 		// 	width: 100%;
  		// 		// }
  		// 		// .break {
  		// 		// 	// width: 100%;
  		// 		// }
  		// 		// .second {
  		// 		// 	width: 100%;
  		// 		// }
				// }
  		// }

  		.divider {
  			flex: 1;
  		}

  		.heading {
  			color: #249fbe;
				font-size: 80.5px;
				text-transform: uppercase;
				font-family: 'Brandon Black';
			  display: flex;
			  flex-direction:column;
			  align-items:flex-end;
		    flex: 2;

			  .row {
			    display:flex;
			    flex-direction:row;
			    width:50%;

			    .box {
			      flex: 1;

			      &.big-flex {
			      	flex: 3;
			      }

			      &.text {
			        flex: 10 0 auto;
			      }

			      &.align-right {
			        text-align:right;
			      }
			    }
			  }
			}

  		.footer {
  			display: flex;
				align-items: center;

				.copyright {
					flex: 3;
					font-family: 'Proxima Nova';
					font-size: 14px;
					color: #9db7c1;
				}

				.copyright-mobile {
					display: none;
				}

  			.social {
					flex: 1;
  				display: flex;
					align-items: center;

					.box {
						margin: 8px;

						i {
							color: #1d9dbd;
							font-size: 15px;
						}
					}

  			}
  			.cta {
  				padding-left: 40px;
					font-family: 'Brandon Bold';
					font-size: 14px;

					a {
						text-decoration: none;
						color: #1d9dbd;
					}
  			}

  			ul.mobile-bottom-navigation {
  				display: none;
  			}
  		}
  	}
  }


}

.side-nav {
	position: absolute;
	top: 0;
	right: 40px;
	height: 100%;
	border-left: 1px solid #cadbe0;
}


@media @smartphones,
@tablets {
  // @import 'media';
  #index {
  	// background-position: -140px 0;
  	background-size: cover;
    padding: 35px 20px 20px 20px;

    .navigation-main {
			flex-direction: row;
			justify-content: space-between;
		  align-items: baseline;
		
			.logo {
				width: 100px;
				height: 18px;
				background: url('../images/logo.png') no-repeat 0 0;
				background-size: contain;
			}

			.navigation-bars {
				flex: 1;
			}


    	.left-side {
  			z-index: 500;
    	}

    	.navigation-bars {
      	display: flex;

      	i {
      		 align-self: center;
      		 font-size: 24px;
      	}
      }

      ul.nav {
        display: none;
        flex-direction: column;

        &.open {
          display: flex;
          position: absolute;
          margin: 0 auto;
          background: rgb(255,255,255,.3);
          width: 100%;
				  left: 0;
				  text-align: center;
				  height: 100%;
				  justify-content: center;

        }
      }

      .languages-mobile {
      	display: block;
  			z-index: 500;
			  align-self: flex-end;
      
      	ul {
      		padding: 0;
      		margin: 0;
      		list-style: none;

      		li {
    			  color: #1d9cbd;
					  background: #eff7f9;
					  border: 2px solid #cadbe2;
					  border-radius: 50%;
					  padding: 5px;
					  text-transform: uppercase;
					  font-size: 13px;
					  font-family: 'Brandon Black';
      		}
      	}
      }

    }
    .content {
      .main-content {
        .heading {
      		justify-content: flex-end;
  				font-size: 250%;

          .row {
          	width: 100%;

          	.box {
				      &.big-flex {
				      	// flex: 0;
				      }
				    }
          }
          // .first, .second {
          // 	width: auto;
          // }

          // .aligner {
						// display: flex;
						// justify-content: space-around;
						// width: 100%;
						// flex-wrap: wrap;
          // }
        }
      }
      .footer {
    	  flex-direction: column;
				align-items: flex-start !important;

        .copyright {
        	display: none;
        }
        .copyright-mobile {
        	order: 1;
        	display: block !important;
					font-family: 'Proxima Nova';
					font-size: 14px;
					color: #9db7c1;
        }
        .social {
        	order: 0;

        	.box {
        		i {
	        		color: #1d9dbd !important;
        		}

        		&:first-child {
        		margin-left: 0;
	        	}
        	}
        }

        .cta {
        	display: none;
        }

        ul.mobile-bottom-navigation {
        	list-style: none;
        	padding: 0;
        	margin: 0;
        	display: flex !important;

        	&:before {
        		content: ' ';
        		height: 3px;
        		width: 100px;
        		background: red;
        	}
        }
      }
    }
  }
}

@media @tablets {
	.heading {
		justify-content: flex-end;
		font-size: 250%;

    .row {
    	width: 100%;

    	.box {
	      &.big-flex {
	      	flex: 10 !important;
	      }
	    }
    }
  }
}

@media @smartphones {
	.heading {
		justify-content: flex-end;
		font-size: 250%;

    .row {
    	width: 100%;

    	.box {
	      &.big-flex {
	      	flex: 0 !important;
	      }
	    }
    }
  }
}


