@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 650px) and (max-width: 959px)";



#contact {
  background: url('../images/backgrounds/contact.jpg');
  background-size: cover;
  background-color: #eff8f9;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;



  .left-side {
		display: flex;
		flex: 1;
		flex-direction: column;
    justify-content: space-between;
  }


  .content {
  	height: 100%;
  	display: flex;
  	flex-direction: column;
    
  align-items: center;
  	// padding-top: 10vh;
  	& > * {
  		// flex: 1
  	}

    .headline {
      color: #249fbe;
      margin-top: 0;
      font-size: 80.5px;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      margin-bottom: 0;
      margin-top: 100px;
    }

  	.main-content {
      padding-top: 5%;
  		display: flex;
      margin-left: 30px;
	  	flex-direction: column;
			align-items: stretch;
			flex: 1;
  		& > * {
  			// display: flex;
	  		// flex: 1
	  	}

  		.divider {
  			flex: 1;
  		}

  		

			.content {
				display: flex;

        & > * {
          flex: 1;
        }
				.left-side {

				}

				.right-side {
          .contact-list {
            display: flex;
            flex-direction: column;

            li {
              // height: 50px;
              margin: 5px 0;
              display: flex;
              align-items: center;
              
              i.fa {
                color: #303030;
                border: 1px solid #e5e5e5;
                background: #fff;
                border-radius: 50%;
                padding: 8px;
              }

              a {
                margin-left: 30px;
                font-family: 'Proxima Nova';
                color: #009ec0;
                font-style: 24px;
                text-transform: uppercase;
                text-decoration: none;
                font-weight: bold;
              }            
            }
          }

          .qr {
            margin-top: 50px;
          }
				}
			}

  		
  	}
  }
}

footer.footer {
      display: flex;
      align-items: center;

      .copyright {
        flex: 3;
        font-family: 'Proxima Nova';
        font-size: 14px;
        color: #9db7c1;
      }

      .copyright-mobile {
        display: none;
      }

      .social {
        flex: 1;
        display: flex;
        align-items: center;

        .box {
          margin: 8px;

          i {
            color: #1d9dbd;
            font-size: 15px;
          }
        }

      }
      .cta {
        padding-left: 40px;
        font-family: 'Brandon Bold';
        font-size: 14px;

        a {
          text-decoration: none;
          color: #1d9dbd;
        }
      }

      ul.mobile-bottom-navigation {
        display: none;
      }
    }