@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 650px) and (max-width: 959px)";



#portfolio {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;



  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
  }

  .content {
    height: 100%;
    display: flex;

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      
      & > * {
        // display: flex;
        // flex: 1
      }

      

      .divider {
        flex: 1;
      }

      .headline {
        color: #249fbe;
        margin-top: 0;
        font-size: 80.5px;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        display: flex;
        flex-direction:column;
        align-items:flex-start;
        flex: 2;
        // align-self: flex-end;
        .row {
          display:flex;
          flex-direction:row;
          width:50%;

          .box {
            flex: 1;

            &.big-flex {
              flex: 3;
            }

            &.text {
              flex: 10 0 auto;
            }

            &.align-right {
              text-align:right;
            }
          }
        }
      }

      section.content {
        // display: flex;
        // justify-content: space-around;
        // flex-direction: column;
        // align-items: center;
        // overflow: auto;
        flex: 1 0 auto;
        justify-content: space-around;
        align-items: center;
       
        ul.portfolios {
          display: flex;
          flex-direction: column;

          li {
            a {
              color: #000;
              font-family: 'Brandon Black';
              font-size: 14px;
              text-decoration: none;
              text-transform: uppercase;

              &:hover {
                color: #ff7d00;
              }
            }

            &.active {

              &:before {
                content: ' ';
                display: block;
                background: #cddee3;
                width: 100%;
                height: 1px;
                position: absolute;
                margin-top: 9px;
                left: 0;
                z-index: -1;

              }

              a {
                color: #ff7d00;
              }
            }
          }
        }

        article {
          display: flex;
          width: 80%;
          flex-direction: column;
          flex-shrink: 0;
          flex-direction: row;

          .content {
            display: flex;
            margin-left: 130px;
            flex-direction: column;
            
            h4 {
              color: #009ec0;
              font-family: 'Brandon Black';
              font-size: 34px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;
            }

            .image {
              img {
                max-width:100%;
                max-height:100%;
              }
            }
          }
        }
      }
    }
  }
}