@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 650px) and (max-width: 959px)";



#blog {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;



  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
  }

  .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    // padding-top: 10vh;
    & > * {
      // flex: 1
    }

    .headline {
      color: #249fbe;
      margin-top: 0;
      font-size: 80.5px;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      margin-bottom: 0;
      margin-top: 100px;
    }
    
    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      
      & > * {
        // display: flex;
        // flex: 1
      }

      .divider {
        flex: 1;
      }

     

      .content {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: center;
        overflow: auto;

        article {
          display: flex;
          width: 80%;
          align-items: flex-start;
          flex-shrink: 0;
          // .image {
          //   img {

          //   }
          // }

          .content {
            display: flex;
            margin-left: 130px;
            
            h4 {
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 34px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;
            }

            .footer {
              display: flex;
              font-family: 'Proxima Nova';
              font-size: 17px;
              flex: 1;
              justify-content: space-between;
              width: 200px;
              align-self: baseline;

              .date {
                color: #b1ccd2;
              }

              a {
                color: #ff7d00;

                &:hover {
                  color: #1f1f1f;
                }
              }
            }
          }
        }
      }
    }
  }
}